function installHideOriginalReplyStyle(): void {
  const host_document = window.parent?.document ?? document;
  const style_id = 'apocalypse-dawn-hide-original-reply-style';
  if (host_document.getElementById(style_id)) return;

  const style = host_document.createElement('style');
  style.id = style_id;
  style.textContent = `
    .mes:has(.TH-render) .mes_text > :not(.TH-render) {
      display: none !important;
    }
  `;
  host_document.head.appendChild(style);
}

function asNumber(value: unknown, fallback = 0): number {
  const numeric = Number(value);
  return Number.isFinite(numeric) ? numeric : fallback;
}

function rollD6(): number {
  return Math.floor(Math.random() * 6) + 1;
}

const warmaid_rank_steps = [
  { name: '', need: 10 },
  { name: '', need: 50 },
  { name: '', need: 100 },
  { name: '', need: 500 },
  { name: '', need: 1000 },
  { name: '', need: 2000 },
  { name: '', need: 6000 },
] as const;

function reconcileGrowthLevel(stat_data: any): void {
  if (!stat_data || typeof stat_data !== 'object') return;

  const profession = String(_.get(stat_data, '..', ''));
  const is_warmaid = profession === '' || String(_.get(stat_data, '..', '')).includes('');
  const is_authority = profession === '' || String(_.get(stat_data, '..', '')).includes('');

  if (is_warmaid) {
    let level = _.clamp(asNumber(_.get(stat_data, '..', 1), 1), 1, warmaid_rank_steps.length);
    let shards = Math.max(0, asNumber(_.get(stat_data, '..', 0), 0));

    // /
    while (level < warmaid_rank_steps.length) {
      const need = warmaid_rank_steps[level - 1].need;
      if (shards < need) break;
      shards -= need;
      level += 1;
    }

    _.set(stat_data, '..', level);
    _.set(stat_data, '..', shards);
    _.set(
      stat_data,
      '..',
      level < warmaid_rank_steps.length ? warmaid_rank_steps[level - 1].need : 0,
    );
  }

  if (is_authority) {
    const shard = Math.max(0, asNumber(_.get(stat_data, '..', 0), 0));
    const has_origin = Boolean(_.get(stat_data, '..', false));

    let level = 1; // 
    if (shard >= 50) level = 2; // 
    if (shard >= 100) level = 3; // 
    if (shard >= 1000 && has_origin) level = 4; // 

    _.set(stat_data, '..', level);
  }
}

function trimBattleLogs(stat_data: any, keep = 3): void {
  if (!stat_data || typeof stat_data !== 'object') return;
  const logs = _.get(stat_data, '', {});
  if (!_.isObjectLike(logs)) {
    _.set(stat_data, '', {});
    return;
  }
  const entries = Object.entries(logs as Record<string, string>);
  if (entries.length <= keep) return;
  _.set(stat_data, '', Object.fromEntries(entries.slice(-keep)));
}

